export const setIsSearching = (value) => ({ type: 'SET_IS_SEARCHING', payload: value })
export const setResult = (data) => ({ type: 'SET_RESULT', payload: data })
export const setSearchError = (error) => ({ type: 'SET_SEARCH_ERROR', payload: error })
export const setSearchString = (str) => ({ type: 'SET_SEARCH_STRING', payload: str })
export const clearSearch = () => ({ type: 'CLEAR_SEARCH' })

export const findPrivateQuizInfo = (quizID) => {
  return (dispatch, getState, { getFirestore }) => {
    const firestore = getFirestore()

    dispatch(setIsSearching(true))
    dispatch(setSearchString(quizID))
    firestore.collection('privateQuizes').doc(quizID).get()
      .then(doc => {
        if (doc.exists) {
          const data = doc.data()
          const result = data.description

          dispatch(setResult(result))
        } else {
          console.log("No such document!")
        }
      })
      .catch(error => {
        console.log("Error getting document:", error)
        dispatch(setSearchError('Searching error'))
      })
      .finally(() => {
        dispatch(setIsSearching(false))
      })
  }
}