import React from 'react'
import { connect } from 'react-redux'
import { compose } from 'redux'
import { withRouter, Link } from 'react-router-dom'
import { Card } from 'react-bootstrap'
import SearchForm from '../components/forms/Search-form/Search-form'
import { findPrivateQuizInfo, clearSearch } from '../reducers/action-creators/search-AC'
import Preloader from '../components/UI/Preloader/Preloader'

class SearchContainer extends React.Component {
  onSubmit = (data) => {
    this.props.findPrivateQuizInfo(data.quizID)
  }

  componentWillUnmount() {
    this.props.clearSearch()
  }

  render() {
    const { isSearching, result, error, searchString } = this.props.search
    const location = {
      pathname: `/quiz/${searchString}`,
      state: { fromSearch: true }
    }

    return (
      <>
        {isSearching ? <Preloader /> : <SearchForm onSubmit={this.onSubmit} />}
        {error ? <div>Ошибка...</div> : null}
        {result ? (
          <Card bg="light" border="light">
            <Card.Header>{result.chapter}</Card.Header>
            <Card.Body>
              <Card.Title>{result.title}</Card.Title>
              <Card.Text>
                <Link to={location}>Перейти на страницу теста</Link>
              </Card.Text>
            </Card.Body>
          </Card>
        ) : null}
      </>
    )
  }
}

const mapStateToProps = ({ search }) => {
  return {
    search
  }
}

const mapDispatchToProps = {
  findPrivateQuizInfo,
  clearSearch
}

export default compose(
  connect(mapStateToProps, mapDispatchToProps),
  withRouter
)(SearchContainer)